body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #f3f5f6;
}

main {
	margin-top: 70px;
}

.header {
	background-color: #1f1f22;
	color: #fff;
	position: fixed;
	top: 0;
	width: 100%;
	height: 70px;
	line-height: 64px;
}

.header--title {
	cursor: pointer;
	display: inline-block;
	margin: 0 5vw;
	font-size: calc(15px + 1vw);
	vertical-align: middle;
	height: 100%;
}

.header--search-bar {
	margin: 0 auto;
	display: inline-block;
	height: 100%;
	border: 0;
}

.header--search-bar input {
	width: 220px;
	height: 34px;
	padding-left: 5px;
	background-color: #d2d2d3;
	outline: 0;
	border: 0;
	border-radius: 2px;
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
	-webkit-transition: background-color .25s ease-in-out,left .25s ease-in-out,width .25s ease-in-out,opacity .25s ease-in-out;
	transition: background-color .25s ease-in-out,left .25s ease-in-out,width .25s ease-in-out,opacity .25s ease-in-out;
}

.header--search-bar button {
	height: 36px;
	border: 0;
	outline: 0;
	border-radius: 2px;
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
	cursor: pointer;
	-webkit-transition: background-color .25s ease-in-out,left .25s ease-in-out,width .25s ease-in-out,opacity .25s ease-in-out;
	transition: background-color .25s ease-in-out,left .25s ease-in-out,width .25s ease-in-out,opacity .25s ease-in-out;
	background: url(/flickr-photos/static/media/magnifier.64d25256.svg);
	background-position: center;
	background-size: 20px;
	background-repeat: no-repeat;
	background-color: #d2d2d3;
	width: 35px;
	vertical-align: middle;
	padding: 0;
}

.header--search-bar input:focus,
.header--search-bar input:focus + button {
	background-color: #fff;
}

.header--search-bar button:disabled {
	background: url(/flickr-photos/static/media/magnifier_disabled.299da546.svg);
	background-position: center;
	background-size: 20px;
	background-repeat: no-repeat;
	background-color: #d2d2d3;
	cursor: not-allowed;
	border: 0;
}

.header--search-bar input:focus {
	width: 300px;
}

::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #848485;
	opacity: 1; /* Firefox */
}

::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #848485;
	opacity: 1; /* Firefox */
}

::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #848485;
	opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
	color: #848485;
}

::-ms-input-placeholder { /* Microsoft Edge */
	color: #848485;
}

@media (max-width: 570px) {
	main {
		margin-top: 105px;
	}

	.header {
		height: 105px;
		line-height: 40px;
	}

	.header--title {
		margin: 0;
		height: 40px;
		width: 100%;
		text-align: center;
		vertical-align: top;
		font-size: 1.55rem;
		margin-top: 5px;
	}

	.header--search-bar {
		height: auto;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-top: 6px;
	}

	.photo-list--no-results,
	.photo-list--error-message {
		font-size: 1rem;
	}
}
.photos-list { 
	padding: 20px;
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
	grid-gap: 15px;
	align-items: stretch;
}

.photos-list--card {
	padding: 10px;
	border-radius: 5px;
	background: #fff;
	word-wrap: break-word;
	border: 1px solid #ccc;
	box-shadow: 2px 2px 6px 0px  rgba(0,0,0,0.3);
	max-width: 100%;
}

.photos-list--card img {
	max-width: 100%;
	display: block;
	margin: 0 auto;
	max-height: 300px;
}

.photo-list--no-results,
.photo-list--error-message {
	text-align: center;
	grid-column: 1/-1;
	font-size: 1.5rem;
	font-weight: bold;
	word-wrap: break-word;
}

.photo-list--error-message {
	color: red;
}

.results-for {
	padding: 20px;
	padding-bottom: 0;
	font-size: calc(14px + 0.5vw);
	font-weight: bold;
	word-wrap: break-word;
}

a {
	text-decoration: none;
}
.loading {
    text-align: center;
    grid-column: 1/-1;
}

.loading-img {
    width: 200px;
}
